import React, { useState,useEffect } from "react"; 
import PropTypes from "prop-types"; 
import { Link, withRouter } from "react-router-dom"; 
import { withStyles } from "@material-ui/core/styles"; 
import Grid from "@material-ui/core/Grid"; 
import Button from "@material-ui/core/Button"; 
import Typography from "@material-ui/core/Typography"; 
import TextField from "@material-ui/core/TextField"; 
import ReactSpeedometer from "react-d3-speedometer" 
import Divider from "@material-ui/core/Divider"; 
import LineChart from "./DBHelpers"; 
import SliderBar from "./SliderBar"; 
import Paper from '@material-ui/core/Paper'; 
import Spinner from "../Helpers/Spinner"; 
import Speedometer from "../Helpers/Speedometer"; 
import BarChartComponent from "../Helpers/BarChart";

const styles = theme => ({ 
 dashBoardContainer: { 
    marginTop:"180px", 
    minHeight: "80px",
    width:"100%",
    margin:"0 auto",
    marginBottom:"20px", 
    boxSizing: "border-box", 
     [theme.breakpoints.down("sm")]:{ },
     [theme.breakpoints.down("xs")]:{
         marginTop:"140px", 
     },
 },
 header:{
 textTransform:"uppercase", 
 backgroundColor:"#000099",
 textAlign:"center",
 color:"white",
 fontSize:"20px",
 fontWeight:"bold",
 padding:"10px",
 margin:"0px 0px 30px 0px",     
 [theme.breakpoints.down("xs")]:{
     fontSize:"15px",
     height:"35px",
 },    
},
displayNoneForXS:{
    [theme.breakpoints.down("xs")]:{
        display:"none"
    }
},
containerDiv:{
    border:"1px solid gray",
    padding:"10px",
    backgroundColor:"#e6e6e6"
},
navigationDiv:{
  textTransform:"capitalize",    
  height:"60px",
  margin:"0px 0px 10px 0px", 
  borderRadius:"0px",
  backgroundColor:"#24444c",
  textAlign:"center",
  padding:"30px 5px 5px 5px",
  fontWight:"bold",  
    fontSize:"15px",
  color:"white",
     "&:hover": { 
      backgroundColor: "#365c66", 
        cursor: "pointer" 

    }, 
},
lblDiv:{
  height:"69px",
  margin:"0px 0px 10px 0px", 
  borderRadius:"0px",
  backgroundColor:"",
     boxSizing: "border-box",
  textAlign:"center",
  padding:"10px",
  fontWight:"bold",
     [theme.breakpoints.down("xs")]:{
          margin:"0px 0px 0px 0px",
     }
},    
motorStatusTab:{
    //border:"1px solid gray", 
    borderRadius:"0px",
    padding:"10px"
},
    moterStatusHeader:{
        textTransform:"uppercase",
        fontWeight:"bold",
        fontSize:"18px",
        margin:"0px 0px 30px 0px"
    },
    marginBtm:{
        marginBottom:"10px"
    },
     startBtn: { 
    textTransform: "capitalize", 
    backgroundColor: "#004d00", 
    borderRadius: "5px", 
    width: "100%", 
    margin: "10px 0", 
    fontSize: "15px", 
    boxShadow: "none",
    outline: "none", 
    color: "#fff", 
    border: "none",
    fontWeight: "bold", 
    fill: "#000", 
    padding:"5px", 
    "&:hover": { 
      backgroundColor: "#008000", 
      color: "#fff", 
        cursor: "pointer" 
    },  
      [theme.breakpoints.down("md")]:{ 
      }, 
    [theme.breakpoints.down("sm")]:{ 
      }, 
      [theme.breakpoints.down("xs")]:{ 
      } 
  },  
    stopBtn: { 
    textTransform: "capitalize", 
    backgroundColor: "#ff0000", 
    borderRadius: "5px", 
    width: "100%", 
    margin: "10px 0", 
    fontSize: "15px", 
    boxShadow: "none",   
    outline: "none", 
    color: "#fff", 
    border: "none", 
    fontWeight: "bold", 
    fill: "#000", 
    padding:"5px", 
    "&:hover": { 
      backgroundColor: "#ff3333", 
      color: "#fff", 
        cursor: "pointer" 
    },  
      [theme.breakpoints.down("md")]:{ 
      }, 
      [theme.breakpoints.down("sm")]:{ 
      }, 
      [theme.breakpoints.down("xs")]:{ 
      } 
  },  
    btnDisabled: { 
    opacity: "0.65", 
    cursor: "not-allowed", 
    textTransform: "capitalize", 
    backgroundColor: "#004d00", 
    borderRadius: "5px", 
    width: "100%", 
    margin: "10px 0", 
    fontSize: "15px", 
    boxShadow: "none", 
    outline: "none", 
    color: "#fff", 
    border: "none", 
    fontWeight: "bold", 
    fill: "#000", 
    padding:"5px", 
        [theme.breakpoints.down("md")]:{ 
      }, 
    [theme.breakpoints.down("sm")]:{ 
      }, 
      [theme.breakpoints.down("xs")]:{ 
      }, 
  },
     sliderPaper:{
        padding:"10px 10px 20px 10px",
    },
     
    frequencyBtn:{
        width:"100%",
        border:"1px solid gray",
        backgroundColor: "#008000",
        fontWeight:"bold",
        fontSize:"12px",
        padding:"5px",
        color:"white",
        margin:"20px 0px 0px 5px",
        "&:hover": { 
      backgroundColor: "#008000", 
      color: "#fff", 
        cursor: "pointer" 
    },
        [theme.breakpoints.down("xs")]:{
        fontSize:"12px",
            marginTop:"15px"
        }
    },
    paperSpeedometer:{
        padding:"10px",
        textAlign:"center",
        height:"200px",
        [theme.breakpoints.down("md")]:{
         padding:"10px",
        height:"200px",
        },
        [theme.breakpoints.down("sm")]:{
         padding:"10px",
        height:"200px",
        }
    },
    spinnerDiv:{
    marginTop:"250px",
    textAlign:"center",
    marginBottom:"250px"
},
    inputTextBox:{
        height:"40px",
        width:"100%",
        padding:"5px",
    },
    alarmContent:{
        padding:"40px 5px 15px 5px",
        textAlign:"center",
        [theme.breakpoints.down("xs")]:{
           padding:"15px 5px 15px 5px", 
        }
    }

});


function DashBoardComponentNew(props){
   
  const { classes,  history,motorReaddingData,setMoterData,GetPumpReading } = props; 

 let readdingData = JSON.stringify(motorReaddingData);
    localStorage.setItem("readdingDatalist", readdingData)
console.log("form dashboard", motorReaddingData);
const inputStyle = { WebkitBoxShadow: "0 0 0 1000px white inset" }; 
     console.log(motorReaddingData)
     const chatData=motorReaddingData?motorReaddingData[2]:"";
   
    const dcBusVoltage=chatData?chatData.dcbusvoltage:0;
    console.log("bus voltage",dcBusVoltage);
   const dcBusVoltageValue=parseInt(dcBusVoltage);
    
    const opVoltage=chatData?chatData.outputvoltage:0;
    
    const outPutCurrent=chatData?chatData.outputcurrent:0;
    const outPutCurrentValue=parseInt(outPutCurrent)/10;
    
    const outPutFrequency=chatData?chatData.outputfrequency:0;
    const outPutFrequencyValue=parseInt(outPutFrequency)/100;
    
     const ouputPower=chatData?chatData.ouputpower:0;
    const ouputPowerValue=parseInt(ouputPower)/10;
    const motorRunState=chatData?chatData.runstate:"";
    const totalWaterFlowPerMin=chatData?chatData.waterFlowPerMin:"";
    
      
    const [motorStart,setMotorStart]=useState(true);    
    const [enableStart, setEnableStart]=useState(true); 
    const [currentStatus,setCurrentStatus ]=useState("Running") 

    const startMotor=()=>{ 
        //setCurrentSpreed(525); 
        setEnableStart(true); 
        setCurrentStatus("Running"); 
    } 

     const stopMotor=()=>{ 
        setCurrentStatus("Stop"); 
       // setCurrentSpreed(0); 
         setEnableStart(false); 
    } 

     const condevData=()=>{
         history.push("/Datatable"); 
         //setMoterData(motorReaddingData);        
         
     } 


    const data = [ 50, 10, 40, 95, -4, -24, 85, 91, 35, 53, -53, 24, 50, -20, -80 ]; 
    console.log("niru",motorReaddingData);
    
   
      const getMoterData=()=>{
         var IMEINub = JSON.parse(localStorage.getItem("imeiNumber"));
         if(IMEINub){
             GetPumpReading(IMEINub)
         }
    }
    
    useEffect(() => {
     setInterval(() => {
        getMoterData();        
     },30000);
     });
     
   
     //<div className={classes.header}>Dashboard & Calculation for cg drive</div>
    //motorReaddingData.length===undefined? <div className={classes.spinnerDiv}><Spinner /></div>:
    return(motorReaddingData.length===undefined? <div className={classes.spinnerDiv}><Spinner /></div>:
     <div className={classes.dashBoardContainer} >  
          
           <Grid container>
           <Grid item lg={12} md={12} sm={12} xs={12}>
           <Grid container spacing={8}>
           <Grid item lg={2} md={2} sm={2} xs={2} className={classes.displayNoneForXS} >
           <div className={classes.containerDiv} >
           <div onClick={()=>condevData()}><Paper  className={classes.navigationDiv} >generate reports</Paper></div>
           <div><Paper  className={classes.navigationDiv}>controller information</Paper></div>
           <div><Paper  className={classes.navigationDiv} >controller geo location Information</Paper></div>
           <div><Paper  className={classes.navigationDiv}>generate Issue Ticket </Paper></div>
           <div><Paper  className={classes.navigationDiv}>downloads</Paper></div>
           </div>
           </Grid>
           
           
           <Grid item lg={10} md={10} sm={10} xs={12}>
           <Grid constainer spacing={8}>
           <Grid item lg={12} md={12} sm={12} xs={12}>
           <Grid container spacing={8}>
        
        
           <Grid item lg={9} md={9} sm={9} xs={12}>
        <Grid container spacing="16">
        <Grid item lg={12} md={12} sm={12} xs={12}>
        <Grid container spacing="8">        
            <Grid item lg={8} md={8} sm={8} xs={12}>
                    <div><Paper className={classes.motorStatusTab}>
            <Grid container spacing={18}>
            <Grid item lg={8} md={8} sm={8} xs={8}>
            <div className={classes.moterStatusHeader}>Motor Status</div>
            <div className={classes.marginBtm}><b>Status:{motorRunState==0?"  Stop":"  Running"}</b></div>
            <div><b>Motor Speed:{" 0" }</b></div>
            
            </Grid>
          <Grid item lg={4} md={4} sm={4} xs={4}>            
            <div><Button className={motorRunState==1?classes.btnDisabled:classes.startBtn}>Start</Button></div>
            <div><Button className={classes.stopBtn} >Stop</Button></div>            
        </Grid>
            </Grid>
            </Paper></div>   
            </Grid>
          <Grid item lg={4} md={4} sm={4} xs={12}>            
               <div ><Paper className={classes.motorStatusTab}>
            <div style={{textAlign:"center", fontWeight:"bold",padding:"10px"}} >Alarm</div>
        <Divider style={{height:"2px"}} />        
         
        <div className={classes.alarmContent}>.</div>
            </Paper></div>        
        </Grid>      
        </Grid>
        
        </Grid>
        <Grid item lg={12} md={12} sm={12} xs={12}>
        
        <Paper className={classes.sliderPaper}>
           <Grid container spacing={8} >
               <Grid item lg={4} sm={4} xs={4}>
                <div>
                   <TextField placeholder="Set Actual Frequency" margin="normal" variant="outlined" className={classes.inputTextBox}
                          value="" inputProps={{ style: inputStyle }}/> 
                    <Button className={classes.frequencyBtn}>Set Actual Frequency</Button>
                   </div>
                <div ></div>
                </Grid>

               <Grid item lg={4} sm={4} xs={4}>
                   <div>
                   <TextField placeholder="Set Upper Limit Frequency" margin="normal" variant="outlined" className={classes.inputTextBox}
                          value="" inputProps={{ style: inputStyle }}/>
                   </div>
                    <div><Button className={classes.frequencyBtn}>Set Upper frequency</Button></div>
                   </Grid>
               <Grid item lg={4} sm={4} xs={4}>
                   <div>
                   <TextField placeholder="Set Rated Current" margin="normal" variant="outlined" className={classes.inputTextBox}
                          value="" inputProps={{ style: inputStyle }}/>
                   </div>
                    <div><Button className={classes.frequencyBtn}>Set Rated Current</Button></div>
                   </Grid>
            </Grid>
        </Paper>
        </Grid>
        
        </Grid>
        </Grid> 
        
        
        <Grid item lg={3} md={3} sm={3} xs={12}>
        <Grid container>
        <Grid item lg={12} md={12} sm={12} xs={6}>
        <Paper  className={classes.lblDiv}>
        <div><b>Water Flow / Min</b></div>
        <div style={{marginTop:"10px"}}>{totalWaterFlowPerMin + "  Liters"}</div>
        </Paper>
        </Grid>
        <Grid item lg={12} md={12} sm={12} xs={6}>
        <Paper  className={classes.lblDiv}>
        <div><b>Total Water Flow</b></div>
        <div style={{marginTop:"10px"}}>230000 Liters</div>
        </Paper>
        </Grid>
        <Grid item lg={12} md={12} sm={12} xs={6}>
        <Paper  className={classes.lblDiv}>
        <div><b>Total Run Hours</b></div>
        <div style={{marginTop:"10px"}}>-</div>
        </Paper>
        </Grid>
        <Grid item lg={12} md={12} sm={12} xs={6}>
        <Paper  className={classes.lblDiv}>
        <div><b>Output Power</b></div>
        <div style={{marginTop:"10px"}}>{ouputPowerValue + "   kW"} </div>
        </Paper>
        </Grid>
        

        </Grid>
        
        </Grid>
           </Grid>
        </Grid>
           
        <Grid item lg={12} md={12} sm={12} xs={12}>
        <Grid container spacing={8}>
         <Grid item lg={3} md={3} sm={3} xs={12}>
         <Paper className={classes.paperSpeedometer} > 
      <div style={{marginBottom:"30px", fontSize:"12px"}}><strong>OUTPUT CURRENT : {outPutCurrentValue+ " A"}</strong></div> 
      <Speedometer width={200} height={200  }  mValue={25} value={outPutCurrentValue} />
     </Paper>
        </Grid>
        <Grid item lg={3} md={3} sm={3} xs={12}>
         <Paper className={classes.paperSpeedometer} > 
      <div style={{marginBottom:"30px",fontSize:"12px"}}><strong>DC BUS VOLTAGE : {opVoltage+ " V"} </strong></div> 
           <Speedometer width={200} height={200}  mValue={2000} value={opVoltage} />
     </Paper>
        </Grid>
         <Grid item lg={3} md={3} sm={3} xs={12}>
         <Paper className={classes.paperSpeedometer} > 
      <div style={{marginBottom:"30px",fontSize:"12px"}}><strong>PV INPUT VOLTAGE : { dcBusVoltageValue +" V"}</strong></div> 
      <Speedometer width={200} height={200}  mValue={2000} value={dcBusVoltageValue} />
     </Paper>
        </Grid>
         <Grid item lg={3} md={3} sm={3} xs={12}>
         <Paper className={classes.paperSpeedometer} > 
      <div style={{marginBottom:"30px",fontSize:"12px"}}><strong>OUTPUT FREQUENCY : {outPutFrequencyValue + " Hz"}</strong></div> 
      <Speedometer width={200} height={200}  mValue={200} value={outPutFrequencyValue} />
     </Paper>
        </Grid>
        </Grid>        
        </Grid>
        <Grid item lg={12} md={12} sm={12} xs={12}>
        <Grid container spacing={8}>
        <Grid item lg={6} md={6} sm={6} xs={12}>
        <Paper>
            <LineChart /></Paper>
        </Grid>
        <Grid item lg={6} md={6} sm={6} xs={12}><Paper><BarChartComponent /></Paper></Grid>
        </Grid>
        </Grid> 
           </Grid>
           </Grid>
           </Grid>
           </Grid>
           </Grid>
    </div>
    )
}
DashBoardComponentNew.propTypes = { 
  classes: PropTypes.object.isRequired 
}; 
export default withStyles(styles)(withRouter(DashBoardComponentNew));