import React, { useState, useEffect } from "react"; 
import PropTypes from "prop-types"; 
import { withRouter, Link } from "react-router-dom"; 
import { withStyles } from "@material-ui/core/styles"; 
import AppBar from "@material-ui/core/AppBar"; 
import Toolbar from "@material-ui/core/Toolbar"; 
import Typography from "@material-ui/core/Typography"; 
import NavigationMenu from "../Navigation"; 
import Menu from "@material-ui/core/Menu"; 
import MenuItem from "@material-ui/core/MenuItem"; 
import IconButton from "@material-ui/core/IconButton"; 
import MenuIcon from "@material-ui/icons/Menu"; 
import Drawer from "@material-ui/core/Drawer"; 
import List from "@material-ui/core/List"; 
import Divider from "@material-ui/core/Divider"; 
import ListItem from "@material-ui/core/ListItem"; 
import ListItemIcon from "@material-ui/core/ListItemIcon"; 
import ListItemText from "@material-ui/core/ListItemText"; 
import logo from "./Logo/mec_Logo.jpeg"; 
const styles = theme => ({ 

  grow: { 
    flexGrow: 1 
  }, 

  appBar: { 
    backgroundColor: "white", 
    width: "100%", 
    position:"fixed",
    boxSizing: "border-box", 
    left: 0, 
    boxShadow: "none", 
    color:"black", 
    margin:"0 auto" 
  }, 
 toolBar: { 
    minHeight: "80px", 
    width: "88%", 
   margin:"0 auto", 
    border: "none", 
    borderTop: 0, 
    boxSizing: "border-box", 
     [theme.breakpoints.down("sm")]:{ 
         width:"95%", 
     }, 
     [theme.breakpoints.down("xs")]:{ 
         width:"100%", 
         padding:"0px 5px 0px 5px", 
         boxSizing: "border-box", 

     } 

  },  

   navigationMenu:{ 
    width: "88%", 
    marginLeft: "auto", 
    marginRight: "auto", 
    border: "none", 
    borderTop: 0, 
    boxSizing: "border-box", 
    minHeight:"45px", 
    textAlign:"left",
       
        [theme.breakpoints.down("sm")]:{ 
             width: "95%", 
         }, 
    [theme.breakpoints.down("xs")]:{ 
           width:"100%", 
   padding:"0px 5px 0px 5px", 
      boxSizing: "border-box", 
    } 
   }, 

     navBarDiv:{ 
         backgroundColor:"#004d00", 
         minHeight:"45px", 
         [theme.breakpoints.down("xs")]:{ 
             display:"none" 
         } 
     }, 

    navBarForXS:{ 
        backgroundColor:"#004d00", 
         minHeight:"45px",  
        [theme.breakpoints.up("lg")]:{ 
            display:"none", 
        }, 
        [theme.breakpoints.up("sm")]:{ 
            display:"none", 
        }, 
        [theme.breakpoints.down("xs")]:{ 
            display:"block", 
        } 
    }, 

     headerTextDiv:{ 
         width:"50%", 
         textAlign:"center",
         fontSize:"20px", 
         fontWeight:"bold",  
         color:"#0000b3", 
         [theme.breakpoints.down("lg")]:{ 
             width:"60%",
             fontSize:"26px",
         } ,
          [theme.breakpoints.down("sm")]:{ 
             width:"60%",
             fontSize:"26px",
         } ,
         [theme.breakpoints.down("xs")]:{ 
             width:"100%",
             fontSize:"14px",
             padding:"10px"
         } ,
         "&:hover": { 
      cursor: "pointer "
    } 
     }, 
    headerContactDiv: 
    { 
    width:"25%",
              [theme.breakpoints.down("sm")]: { 
       width:"40%",  
    } , 
    [theme.breakpoints.down("xs")]: { 
      display:"none"     
    }    
    }, 
     headerContactDivForXS: 
    { 
    width:"60%", 
    [theme.breakpoints.down("xs")]: { 
      display:"block",     
    }    
    }, 
    contactTextDiv:{ 
        width:"60%", 
        float:"left",  
        //borderRight:"2px solid gray",  
        marginRight:"10px",  
        paddingRight:"5px", 
        [theme.breakpoints.down("xs")]:{ 
             //borderRight:"2px solid #fff",  
        } 
    }, 
    accIconDivForXS:{ 
        width:"40%",  
        textAlign:"right" 
    }, 
    accDiv:{ 
        width:"auto", 
        float:"left", 
        textAlign:"right", 
        width:"70%" 
    }, 
    colWhite:{ 
        color:"#fff" 
    }, 
    headLabels:{ 
        fontSize:"10px",  
        fontWeight:"bold", 
        color:"#fff" 
    }, 
    menuButtonForXS:{ 
        padding:"5px", 
    }, 
    menuIconDiv:{ 
        width:"10%", 
        [theme.breakpoints.up("lg")]: { 
        display:"none",     
    }, 
        [theme.breakpoints.up("sm")]: { 
        display:"none",     
    }, 
        [theme.breakpoints.down("xs")]: { 
      display:"block",     
    }, 
    }, 
     paper: { 
    height: "calc(100%)", 
    backgroundColor:"#e6e6e6",      
    top: 52, 
    [theme.breakpoints.down("sm")]: { 
      width: "30%" 
    }, 
    [theme.breakpoints.down("xs")]: { 
      width: "72%" 
    } 
  }, 
     menuListItems: { 
    padding: "10px 10px 10px 20px", 
    borderBottom: "1px solid gray" 
  }, 
    ListText: { 
    width: "190px", 
        fontWeight:"bold", 
        fontSize:"14px", 
        color:"black" 
  }, 
    menuListLink:{ 
        display:"flex" 
    },
    navBarLabel:{
    width:"20%", 
    float:"left", 
    color:"white",
    fontWeight:"bold"
},
    logoClass:{
    width:"200px", 
    height:"100px",
    [theme.breakpoints.down("xs")]: { 
      width:"80px", 
    height:"80px", 
    }
}
}); 

 

function HeaderComponent(props) { 
  const { classes, history, loginData} = props; 
    console.log("nnfnfnnfnf",loginData)
    var IMEINub = JSON.parse(localStorage.getItem("imeiNumber"));
    let [anchorEl, setAnchorE1] = useState(null); 
  const handleClick = event => { 
    setAnchorE1(event.currentTarget); 
  }; 
  const [search, setSearch] = useState(""); 
  const handleClose = () => { 
    setAnchorE1(null); 
  }; 
     
let [open, setOpen] = useState(false); 
  const toggleDrawer = () => { 
    setOpen(!open); 
  }; 
    const goToHome=()=>{
        localStorage.removeItem("readdingDatalist");
        localStorage.removeItem("imeiNumber");
        history.push("/");
    }
    
    const signOut=()=>{
        localStorage.removeItem("readdingDatalist");
        localStorage.removeItem("imeiNumber");
        history.push("/");
    }
 
     const menuList = ( 
    <div className={classes.list}> 
      <List> 
        {[ 
          { item: "Home", url: "/" }, 
          { item: "About", url: "/" }, 
          { item: "Services", url: "/" }, 
          { item: "Contact Us", url: "/"}, 
          { item: "Dash Board", url: "/Dashboard" }, 
          { item: "Generate Reports", url: "/DataTable" },
          { item: "Controller Information", url: "/" },
          { item: "Controller Geo Location Information", url: "/" },
          { item: "Generate Issue Ticket", url: "/" },
         { item: "Downloads", url: "/" },
         
        ].map((text, index) => ( 
          <ListItem button key={index} className={classes.menuListItems}> 
            <Link to={text.url} className={classes.menuListLink}> 
              <ListItemText 
                primary={text.item} 
                className={classes.ListText} 
              /> 
              <ListItemIcon> 
                <svg 
                  xmlns="http://www.w3.org/2000/svg" 
                  width="24" 
                  height="24" 
                  viewBox="0 0 24 24" 
                > 
                  <path d="M8.59 16.34l4.58-4.59-4.58-4.59L10 5.75l6 6-6 6z" /> 
                </svg> 
              </ListItemIcon> 
            </Link> 
          </ListItem> 
        ))} 
      </List> 
    </div> 
  ); 
     
  return (<div> 
          <AppBar position="fixed" className={classes.appBar}> 
           <Toolbar className={classes.toolBar}> 
          <div className={classes.menuIconDiv} > 
          <IconButton  
                className={classes.menuButtonForXS} 
                color="inherit" 
                aria-label="Open drawer"   
               onClick={toggleDrawer} 
              > 
                <MenuIcon /> 
              </IconButton> 
          </div>
          <div style={{width:"20%"}}><img src={logo} className={classes.logoClass}  /></div>
           <div className={classes.headerTextDiv} onClick={()=>goToHome()}>SOLAR MOTOR MONITORING SYSTEM                   
           </div> 
           <div className={classes.headerContactDiv} > 
                       <div > 
                            <div><strong>Phone : +919741514646 </strong></div> 
                                <div><strong>Email : shiva@mymecindia.com </strong></div> 
                        </div> 
       </div> 
          <Drawer open={open}  
          onClose={toggleDrawer} onClick={toggleDrawer} classes={{ paper: classes.paper }} > 
                            {menuList} 
          </Drawer> 
           </Toolbar> 
          <div className={classes.navBarDiv}> 
          <Toolbar className={classes.navigationMenu}>  
          <div className={classes.navBarLabel}>Home</div> 
          <div className={classes.navBarLabel}>About</div> 
          <div className={classes.navBarLabel}>Services</div> 
          <div style={{width:"20%", color:"white", fontWeight:"bold"}}>Contact Us</div> 
          <div style={{width:"20%", textAlign:"right" }}> 
                <div style={{width:"auto", float:"left", textAlign:"right",width:"70%"}}> 
                <Typography style={{color:"#fff"}}>{IMEINub?loginData.loginData:""}</Typography></div> 
                   <div > <i 
                      className={`material-icons ${classes.signinForXS}`} 
                      onClick={handleClick} 
                    > 
                      <svg   
                        className={classes.svgMyAcc} 
                        xmlns="http://www.w3.org/2000/svg" 
                        width="24" 
                        height="24" 
                        viewBox="0 0 18 18" 
                        fill="white" 
                      > 
                        <path d="M9 1C4.58 1 1 4.58 1 9s3.58 8 8 8 8-3.58 8-8-3.58-8-8-8zm0 2.75c1.24 0 2.25 1.01 2.25 2.25S10.24 8.25 9 8.25 6.75 7.24 6.75 6 7.76 3.75 9 3.75zM9 14.5c-1.86 0-3.49-.92-4.49-2.33C4.62 10.72 7.53 10 9 10c1.47 0 4.38.72 4.49 2.17-1 1.41-2.63 2.33-4.49 2.33z" /> 
                      </svg> 
                      <span /> 
                    </i> 
                    </div> 
          </div> 
           </Toolbar>           
        </div> 
        <div className={classes.navBarForXS}> 
          <Toolbar className={classes.navigationMenu}>  
          <div className={classes.headerContactDivForXS} > 
                        <div className={classes.contactTextDiv} > 
                            <Typography className={classes.headLabels} >Phone : +919741514646 </Typography> 
                            <Typography className={classes.headLabels} > Email : info@abcd.com </Typography>
                        </div> 
           </div> 
          <div className={classes.accIconDivForXS} > 
                <div className={classes.accDiv} > 
                <Typography className={classes.colWhite}>{IMEINub?loginData.loginData:""}</Typography></div> 
                   <div > <i 
                      className={`material-icons`} 
                      onClick={handleClick} 
                    > 
                      <svg   
                        className={classes.svgMyAcc} 
                        xmlns="http://www.w3.org/2000/svg" 
                        width="24" 
                        height="24" 
                        viewBox="0 0 18 18" 
                        fill="white" 
                      > 
                        <path d="M9 1C4.58 1 1 4.58 1 9s3.58 8 8 8 8-3.58 8-8-3.58-8-8-8zm0 2.75c1.24 0 2.25 1.01 2.25 2.25S10.24 8.25 9 8.25 6.75 7.24 6.75 6 7.76 3.75 9 3.75zM9 14.5c-1.86 0-3.49-.92-4.49-2.33C4.62 10.72 7.53 10 9 10c1.47 0 4.38.72 4.49 2.17-1 1.41-2.63 2.33-4.49 2.33z" /> 
                      </svg> 
                      <span /> 
                    </i> 
                      {IMEINub?(<Menu  
                        id="simple-menu" 
                        anchorEl={anchorEl} 
                        open={Boolean(anchorEl)} 
                        onClose={handleClose} 
                      > 
                        <MenuItem 
                          onClick={() => { 
                            handleClose(); 
                           signOut();
                          }} 
                        > 
                          Sign Out 
                        </MenuItem> 
                        
                      </Menu>) :(<Menu  
                        id="simple-menu" 
                        anchorEl={anchorEl} 
                        open={Boolean(anchorEl)} 
                        onClose={handleClose} 
                      > 
                        <MenuItem 
                          onClick={() => { 
                            handleClose(); 
                            history.push("/login"); 
                          }} 
                        > 
                          Login 
                        </MenuItem> 
                        <MenuItem 
                          onClick={() => { 
                            handleClose(); 
                            history.push("/register"); 
                          }} 
                        > 
                          Register 
                        </MenuItem> 
                      </Menu>) } 
                    </div> 
          </div> 
           </Toolbar>           
        </div> 
          </AppBar> 
          </div>    
  ); 
} 
HeaderComponent.propTypes = {classes: PropTypes.object.isRequired 
}; 
export default withRouter(withStyles(styles)(HeaderComponent)); 
