import React, { useState } from "react"; 
import PropTypes from "prop-types"; 
import { Link, withRouter } from "react-router-dom"; 
import { withStyles } from "@material-ui/core/styles"; 
import Grid from "@material-ui/core/Grid"; 
import Button from "@material-ui/core/Button"; 
import Typography from "@material-ui/core/Typography"; 
import TextField from "@material-ui/core/TextField"; 
import CardContent from "@material-ui/core/CardContent"; 
import Card from "@material-ui/core/Card"; 
import { light } from "@material-ui/core/styles/createPalette"; 
import { gray } from "ansi-colors"; 
import Spinner from "../Helpers/Spinner"; 

const styles = theme => ({ 

  root: { 
    display: "flex", 
    position: "relative", 
    top: "50%", 
    left: "50%",
    transform: "translate(-50%, -50%)", 
    height: "100%", 
    width: "100%", 
    flexDirection: "column", 
    marginTop:"350px",  
      [theme.breakpoints.down("xs")]:{ 
        marginTop:"350px",    
        boxSizing: "border-box", 
        width: "97%", 
        marginBottom:"-150px" 
      } 
  }, 
  lblStyle: { 
    margin: "20px 0px", 
    textAlign: "left" 
  }, 
   txtStyle: { 
    width: "100%", 
    height: "50px" 
  },  
    errorTextBox:{
       width: "100%", 
    height: "50px",
        border:"1px solid red"
    },

   btnStyle: { 
    textTransform: "capitalize", 
    backgroundColor: "#004d00", 
    borderRadius: "5px", 
    height:"50px",
    width: "100%", 
    margin: "10px 0px 0px 0px",
    boxShadow: "none",
    outline: "none", 
    color: "#fff", 
    border: "none",
    fontWeight: "bold",
    fill: "#000",    
    "&:hover": { 
      backgroundColor: "#008000",
      color: "#fff" 
    } 
  }, 
  width100: { 
    width: "100%" 
  }, 
  reghide: { 
    [theme.breakpoints.down("sm")]: { 
      display: "none" 
    } 
  }, 
  rowHeight: { 
    height: "40px" 
  }, 
  rowHeight30: { 
    height: "40px" 
  }, 
  loginHead: { 
    textAlign: "center", 
    fontSize: "24px" 
  }, 
  logo: { 
    width: "100px", 
    height: "40px", 
    display: "block", 
    margin: "0 auto", 
    padding: "50px 0 0 0" 
  }, 
  register: { 
    fontSize: "14px", 
    textAlign: "center" 
  }, 
  spinnerDiv: { 
    textAlign: "center", 
    marginTop: "20%" 
  }, 
  forgotPwd: { 
    textAlign: "right" 
  }, 
    loginHeader:{ 
    color:"#004d00", 
    fontSize:"26px",  
    margin:"15px 0px 30px 0px", 
    textAlign:"center" 
},
spinnerDiv:{
    marginTop:"250px",
    textAlign:"center",
    marginBottom:"250px"
},
}); 


function LoginComponent(props) { 
  const { classes,  history, styleFromHomepage, setLoginDataHeader,GetPumpReading,login } = props; 
    const [userName, setUserName] = useState(""); 
    const handleUsername=(value)=>{ 
        setUserName(value); 
    } 
    
    const [password, setPassword] = useState(""); 
    const handlePassword=(value)=>{ 
        setPassword(value); 
    } 
    
    const [loginError, setLoginError]=useState("");
    const [userNameError, setUserNameError]=useState("");
    const [passwordError, setPasswardError]=useState("");
    const [isLoading, setIsLoading] = useState("");

     const handleSubmit = e => {
         
         e.preventDefault();
         if(userName!=""&&password!="")
         {
            setIsLoading(true);
            login({
           UserName:userName,
           Password:password
         }).then(resp => validateLogin(resp));
        }
        else{
            setLoginError("Enter UserName & password");
        } 
    
  };
  const validateLogin = data => {
      let imeiCode=data?data.payload.username:"";      
      let imeiNumber = JSON.stringify(imeiCode);
      localStorage.setItem("imeiNumber", imeiNumber)
     console.log(data)
      setIsLoading(false);
      if(imeiCode)
          {
            GetPumpReading(imeiCode);
            history.push("/Dashboard") 
          }
      else{
          setLoginError("Error while login");
      }
  };
      

const inputStyle = { WebkitBoxShadow: "0 0 0 1000px white inset" }; 
  return (isLoading? <div className={classes.spinnerDiv}><Spinner /></div>:
    <div className={styleFromHomepage?styleFromHomepage:classes.root}> 
     <div><Typography className={classes.loginHeader} ><strong>MEMBER LOGIN</strong></Typography></div> 
        <Grid container> 
            <Grid lg={4} sm={4} item /> 
            <Grid item lg={4} sm={5} xs={12}> 
              <Card> 
                <CardContent> 
      <Grid container>
      <Grid item lg={12} sm={12} xs={12} >
            <div><Typography style={{marginBottom:"-10px"}}> User Name </Typography> 
                <TextField 
                          placeholder="Enter User name" 
                          margin="normal" 
                          variant="outlined"
                          className={classes.txtStyle}
                          value={userName}
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleUsername(e.target.value)}
                /> 
      </div>     
      </Grid>
<Grid item lg={12} sm={12} xs={12} >
            <div><Typography style={{marginBottom:"-10px"}}> Password </Typography> 
      
                <TextField 
                          placeholder="Enter password" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          type="password" 
                           value={password} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handlePassword(e.target.value)} 
                        /> 
      </div>     
      </Grid>
<Grid item lg={12} sm={12} xs={12} >
            <div>
    <Button  className={classes.btnStyle} onClick={handleSubmit} >LOG IN </Button> 
      </div> 
<div style={{color:"red", marginTop:"20px", textAlign:"center"}}>{loginError?loginError:""}</div>
      </Grid>
      </Grid>
      </CardContent> 
              </Card> 
            </Grid> 
          </Grid> 
    </div> 
  ); 
} 
LoginComponent.propTypes = { 
  classes: PropTypes.object.isRequired 
}; 
export default withStyles(styles)(withRouter(LoginComponent)); 
