import React, {useState,useEffect} from "react"; 
import { Grid, Button } from "@material-ui/core"; 
import { withStyles } from "@material-ui/core/styles"; 
import { Link, withRouter } from "react-router-dom"; 
import Slider from "react-slick"; 
import Featured from "../HeroContentBlock"; 
import Typography from "@material-ui/core/Typography"; 
import TextField from "@material-ui/core/TextField"; 
import CardContent from "@material-ui/core/CardContent"; 
import Card from "@material-ui/core/Card";  
import pic_one from "./images/pic_one.jpg"; 
import pic_three from "./images/pic_three.jpg"; 
import pic_two from "./images/pic_two.jpg"; 
import pic_four from "./images/pic_four.jpg";
import Spinner from "../Helpers/Spinner";
 
const styles = theme => ({ 
 homecomponetContainer: { 
    marginTop:"180px", 
    minHeight: "80px", 
    width: "95%", 
    margin:"0 auto", 
    border: "none", 
    borderTop: 0, 
    boxSizing: "border-box", 
    [theme.breakpoints.down("sm")]:{ 
        width: "90%", 
    }, 
     [theme.breakpoints.down("xs")]: { 
          width: "100%", 
         display:"block",  
          marginLeft: "0px", 
          marginRight: "0px", 
          marginTop:"135px", 
         padding:"0px 0px 0px 0px", 
         boxSizing: "border-box", 
    } 
  }, 

  root: { 
    display: "flex", 
    position: "absolute", 
    top: "52%",  
    left: "70.2%", 
    transform: "translate(-50%, -50%)", 
    height: "100%", 
    width: "65%", 
    flexDirection: "column", 
    marginTop:"200px",   
  },     
loginHeader:{ 
    color:"#004d00", 
    fontSize:"26px",  
    margin:"0px 0px 15px 0px", 
    textAlign:"center" 
}, 
    LoginSection:{ 
        [theme.breakpoints.down("sm")]:{ 
            width:"100%" 
        }, 
        [theme.breakpoints.down("xs")]:{ 
            display:"none" 
        } 
    }, 
    homeHeader:{ 
        fontSize:"26px", 
        color:"#5c8a8a",  
        fontWeight:"bold",  
        height:"50px", 
        [theme.breakpoints.down("xs")]:{ 
           fontSize:"16px", 
            height:"30px", 
            textAlign:"center",
            padding:"10px"
        } 
    }, 
    homeImages:{ 
        width:"229px", 
        height:'200px', 
        [theme.breakpoints.down("sm")]:{ 
           width:"95%", 
            height:'150px', 
        }, 
      [theme.breakpoints.down("xs")]:{ 
           width:"99%", 
        height:'100px', 
        }   
    }, 

     imageDiv:{ 
         margin:"5px", 
         padding:"3px", 
          boxSizing: "border-box", 
         [theme.breakpoints.down("sm")]:{ 
           padding:"0px", 
           margin:"0px", 
            boxSizing: "border-box", 
        } , 
         [theme.breakpoints.down("xs")]:{ 
           padding:"0px", 
           margin:"0px", 
            boxSizing: "border-box", 
        }  
     }, 

    imagesGrid:{ 
        marginTop:"50px", 
        backgroundColor:"#d9d9d9", 
        marginBottom:"20px", 
        [theme.breakpoints.down("xs")]:{ 
            marginTop:"30px", 
        }  

    }, 

    txtStyle: { 
    width: "100%", 
    height: "50px" 

  }, 

    btnStyle: { 

    textTransform: "capitalize", 
    backgroundColor: "#004d00", 
    borderRadius: "5px", 
    height:"50px",
    width: "100%", 
    margin: "10px 0px 0px 0px",
    fontSize: "12px",
    boxShadow: "none",
    outline: "none", 
    color: "#fff", 
    border: "none",
    fontWeight: "bold",
    fill: "#000",    
    "&:hover": { 
      backgroundColor: "#008000",
      color: "#fff" 
    } 
  }, 
    lblStyle: { 
    margin: "10px 0px", 
    textAlign: "left" 
  },
    spinnerDiv:{
    marginTop:"250px",
    textAlign:"center",
    marginBottom:"250px"
},
}); 
 

const HomeComponent = props => { 
  const { classes,history, GetPumpReading,setLoginDataHeader,login } = props; 
 const inputStyle = { WebkitBoxShadow: "0 0 0 1000px white inset" }; 

const formsImages = [ 
  {imageURL:pic_one}, 
  {imageURL:pic_two}, 
  {imageURL:pic_three}, 
  {imageURL:pic_four}, 
]; 

     const [userName, setUserName] = useState(""); 
    const handleUsername=(value)=>{ 
        setUserName(value); 
    } 

    const [password, setPassword] = useState(""); 
    const handlePassword=(value)=>{ 
        setPassword(value); 
    } 
  const [loginError, setLoginError]=useState("");
  const [isLoading, setIsLoading] = useState("");
     

  const handleSubmit = e => {
    e.preventDefault();
    if(userName!=""&&password!="")
        {
            setIsLoading(true);
            setLoginError("");
            login({
            UserName:userName,
            Password:password
    }).then(resp => validateLogin(resp));
        }
        else{
            setLoginError("Enter UserName & Password");
        } 
    
  };
  const validateLogin = data => {
      setIsLoading(false);
      let imeiCode=data?data.payload.username:1234;
      let imeiNumber = JSON.stringify(imeiCode);
      localStorage.setItem("imeiNumber", imeiNumber);
     console.log(data)
      if(imeiCode)
          {
              setLoginError("");
              GetPumpReading(data.payload.username);
              setLoginDataHeader(userName);
              history.push("/Dashboard") 
          }
      else{
           setIsLoading(false);
         setLoginError("Error while login");
      }
  };
    
  return (isLoading? <div className={classes.spinnerDiv}><Spinner /></div>:
      <div className={classes.homecomponetContainer}> 
          <Grid container> 
           <Grid item lg={12} md={12} sm={12} xs={12}> 
            <div className={classes.homeHeader} >Welcome To Solar Motor Monitoring System</div> 
           </Grid> 
          <Grid item lg={12} md={12} sm={12} xs={12}> 
          <Grid container> 
              <Grid item lg={8} md={7} sm={7} xs={12}> 
             <div style={{textAlign:"right"}}><Featured /> </div> 
             </Grid> 
            <Grid item lg={4} md={5} sm={5} xs={12} style={{paddingLeft:"20px"}} className={classes.LoginSection} > 
          <div><Typography className={classes.loginHeader} ><strong>MEMBER LOGIN</strong></Typography></div> 
          <Grid container> 
           <Grid item lg={12} sm={12} xs={12}> 
              <Card> 
                <CardContent> 
            <form  > 
            <div>{loginError?<div style={{color:"red", textAlign:"center"}}>{loginError}</div>:""}</div>
                  <Grid container> 
                 <Grid item lg={1} sm={1} /> 
                    <Grid 
                      item
                      lg={12} 
                      sm={12} 
                      xs={12} 
                      className={classes.rowHeight30} 
                    > 
                      <Typography className={classes.lblStyle}> User name </Typography> 
                    </Grid> 
                    <Grid item lg={1} sm={1} /> 
                </Grid> 
               <Grid container style={{marginTop:"-15px"}}> 
                      <Grid item lg={1} sm={1} /> 
                      <Grid item lg={12} sm={12} xs={12} > 
                        <TextField 
                          placeholder="Enter User name" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={userName} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleUsername(e.target.value)} 
                        /> 
                      </Grid> 
                      <Grid item lg={1} sm={1} /> 
                    </Grid> 
                   <Grid container> 
                 <Grid item lg={1} sm={1} /> 
                    <Grid item lg={12} sm={12} xs={12} className={classes.rowHeight30}> 
                      <Typography className={classes.lblStyle}> 
                       Password 
                      </Typography> 
                    </Grid> 
                    <Grid item lg={1} sm={1} /> 
                </Grid> 

               <Grid container style={{marginTop:"-15px"}}> 

                      <Grid item lg={1} sm={1} /> 

                      <Grid 

                        item 

                        lg={12} 

                      sm={12} 

                      xs={12} 

                        className={classes.rowHeight} 

                      > 

                        <TextField 

                          placeholder="Enter password" 

                          margin="normal" 

                          variant="outlined" 

                          className={classes.txtStyle} 

                          type="password" 

                           value={password} 

                            inputProps={{ style: inputStyle }} 

                          onChange={e => handlePassword(e.target.value)} 

                        /> 

                      </Grid> 

                      <Grid item lg={1} sm={1} /> 

                    </Grid> 

                     <Grid container> 

                      <Grid item lg={1} sm={1} /> 

                      <Grid 

                        item 

                        lg={12} 

                        sm={12} 

                        xs={12} 


                      > 

                        <Button  className={classes.btnStyle} onClick={handleSubmit}> 

                          LOG IN 

                        </Button> 

                      </Grid> 

            </Grid> 

       </form> 

                </CardContent> 

          </Card> 

              

           </Grid> 

          </Grid> 

           

             </Grid> 

          </Grid> 

          </Grid> 

          <Grid item lg={12} md={12} sm={12} xs={12} className={classes.imagesGrid} > 

          <Grid container spacing={8}> 

          {formsImages.map((item, i) => (<Grid  lg={3} md={2} sm={3} xs={3}  item  key={item.imageURL}> 

               <div className={classes.imageDiv}> 

                    <img src={item.imageURL} alt={item.name} className={classes.homeImages} /> 

                </div>     

            </Grid>))} 

          </Grid> 

          </Grid> 

          </Grid> 

        </div> 

         ); 

}; 

export default withStyles(styles)(withRouter(HomeComponent)); 
