import React, { useState,useEffect } from "react"; 
import PropTypes from "prop-types"; 
import { Link, withRouter } from "react-router-dom"; 
import { withStyles } from "@material-ui/core/styles"; 
import Grid from "@material-ui/core/Grid"; 

import ReactSpeedometer from "react-d3-speedometer" 

import Paper from '@material-ui/core/Paper'; 


const styles = theme => ({ });

function Speedometer(props) {
    const {value,mValue, width, height}=props;
    return(<div>
           <ReactSpeedometer maxValue={mValue} value={value}  needleColor="blue"  startColor="red"  segments={5} 
        endColor="green"  needleTransitionDuration={4000}  needleTransition="easeElastic" needleHeightRatio={0.8} 
          width={width} height={height}  forceRender={true}/> 
           </div>);
    
}


export default withStyles(styles)(withRouter(Speedometer)); 