
import React, { useState } from "react"; 
import PropTypes from "prop-types"; 
import { Link, withRouter } from "react-router-dom"; 
import { withStyles } from "@material-ui/core/styles"; 
import Grid from "@material-ui/core/Grid"; 
import Button from "@material-ui/core/Button"; 
import Typography from "@material-ui/core/Typography"; 
import TextField from "@material-ui/core/TextField"; 
import CardContent from "@material-ui/core/CardContent"; 
import Card from "@material-ui/core/Card"; 
import { light } from "@material-ui/core/styles/createPalette"; 
import { gray } from "ansi-colors"; 
import NumberFormat from "react-number-format"; 
 import DropDown from '../Helpers/SelectDropDown'; 
import Spinner from "../Helpers/Spinner";
const styles = theme => ({ 

  root: { 
    display: "flex", 
    position: "relative", 
    top: "50%", 
    left: "30%",
    transform: "translate(-50%, -50%)", 
    height: "100%", 
    width: "100%", 
    flexDirection: "column", 
    marginTop:"580px",
       [theme.breakpoints.down("sm")]:
      { 
          marginTop:"480px", 
            width: "100%", 
          left: "35%",

      } ,
    [theme.breakpoints.down("xs")]:
      { 
          marginTop:"290px", 
          //marginBottom:"80px",
            width: "100%", 
          height: "300px", 
           left: "50%",
         top: "50%", 
      } 
  }, 
  lblStyle: { 
    margin: "20px 0px", 
    textAlign: "left" 
  }, 
  txtStyle: { 
    width: "100%",
    height: "50px",
    marginBottom:"20px",
  }, 
  btnStyle: { 
    textTransform: "capitalize", 
    backgroundColor: "#004d00", 
    borderRadius: "5px", 
    width: "100%", 
    margin: "10px 0", 
    fontSize: "12px", 
    boxShadow: "none", 
    outline: "none", 
    color: "#fff", 
    border: "none", 
    fontWeight: "bold", 
    fill: "#000",
      height:"50px",
    "&:hover": { 
      backgroundColor: "#008000", 
      color: "#fff" 
    } 
  }, 

  width100: { 
    width: "100%" 
  }, 
  reghide: { 
    [theme.breakpoints.down("sm")]: { 
      display: "none" 
    } 
  }, 
  rowHeight: { 
    height: "40px" 
  }, 
  rowHeight30: { 
    height: "50px" 
  }, 
  loginHead: { 
    textAlign: "center", 
    fontSize: "24px" 
  }, 
  logo: { 
    width: "100px", 
    height: "40px", 
    display: "block", 
    margin: "0 auto", 
    padding: "50px 0 0 0" 
  }, 
  register: { 
    fontSize: "14px", 
    textAlign: "center" 
  }, 
  spinnerDiv: { 
    textAlign: "center", 
    marginTop: "20%" 
  }, 
  forgotPwd: { 
    textAlign: "right" 
  }, 
    loginHeader:{ 
    color:"#004d00", 
    fontSize:"26px",  
    margin:"15px 0px 15px  0px", 
    textAlign:"center" 
}, 

     InputText: { 
    width: "100%",
    height: "35px", 
    marginTop: "0px", 
    margin: "0px 0px 10px 0px" 
  },
width98:{
    width:"100%"
},
regRightDiv:{
     width:"96%",
    paddingLeft:"15px",
     [theme.breakpoints.down("sm")]: {
       width:"100%",
           paddingLeft:"0px",
    } 
},
    selectDropDown: { 
    width: "100%", 
    height: "50px", 
    marginBottom:"20px", 
    fontSize:"16px", 
    [theme.breakpoints.down("xs")]: { 
       width: "100%", 
       marginBottom:"20px" ,
            height: "50px", 

    } 
  },
    errorClass:{
        color:"red", 
        textAlign:"center"
    },
    spinnerDiv:{
    marginTop:"250px",
    textAlign:"center",
    marginBottom:"250px"
},
lblDiv:{
  width:"50%",float:"left"
},
errorLbl:{
  marginBottom:"-10px", color:"red", fontSize:"10px"
}
}); 


function RegisterComponent(props) { 

  const { classes,  history, styleFromHomepage, setLoginDataHeader,registration } = props; 

     

    const [userName, setUserName] = useState(""); 
    const handleUsername=(value)=>{ 
        setUserName(value); 
    } 
    
    const [firstName, setFirstName] = useState(""); 
    const handleFirstName=(value)=>{ 
        setFirstName(value); 
    } 
    
    const [lastName, setLastName] = useState(""); 
    const handleLastName=(value)=>{ 
        setLastName(value); 
    } 
    
    const [farmerPhoneNumber, setFarmerPhoneNumber] = useState(""); 
    const handleFarmerPhoneNumber=(value)=>{ 
        setFarmerPhoneNumber(value); 
    }
    const [RMSPhoneNumber, setRMSPhoneNumber] = useState(""); 
    const handleRMSPhoneNumber=(value)=>{ 
        setRMSPhoneNumber(value); 
    }
    const [motorType, setMotorType] = useState(""); 
    const handleMotorType=(value)=>{ 
        setMotorType(value); 
    }
    
     const [motorHP, setMotorHP] = useState(""); 
    const handleMotorHP=(value)=>{ 
        setMotorHP(value); 
    }
    const [motor, setMotor] = useState(""); 
    const handleMotor=(value)=>{
        setMotor(value); 
    }
    
     const [landAddress, setLandAddress] = useState(""); 
    const handleLandAddress=(value)=>{ 
        setLandAddress(value); 
    }
      const [farmerAddress, setFarmerAddress] = useState(""); 
    const handleFarmerAddress=(value)=>{ 
        setFarmerAddress(value); 
    }
    const [password, setPassword] = useState(""); 
    const handlePassword=(value)=>{ 
        setPassword(value); 
    }
    const [confirmPassword, setConfirmPassword] = useState(""); 
    const handleConfirmPassword=(value)=>{ 
        setConfirmPassword(value); 
    }
    
   const [locationLat, setLocationLat] = useState(""); 
    const handleLocationLat=(value)=>{ 
        setLocationLat(value); 
    }
    const [locationLong, setLocationLong] = useState(""); 
    const handleLocationLongt=(value)=>{ 
        setLocationLong(value); 
    }
    const [imeiNumber, setImeiNumber] = useState(""); 
    const handleIMEI=(value)=>{ 
        setImeiNumber(value); 
    }
    
    
     const [errorRegister, setErrorRegister] = useState(""); 
    const [isLoading, setIsLoading] = useState("");
    
    const isRegistration =imeiNumber && firstName && lastName && userName && farmerAddress && farmerPhoneNumber && RMSPhoneNumber && motorType && motorHP && motor && landAddress && password && confirmPassword?true:false;

    const [imeiError, setIMEIError]=useState("");
      const [firstNameError, setFirsNameError]=useState("");
      const [lastNameError, setLastNameError]=useState("");
      const [userNameError, setUserNameError]=useState("");
      const [farmerAddError, setFarmerAddError]=useState("");
      const [farmerPhoneError, setFarmerPhoneError]=useState("");
      const [rmsPhoneError, setRMSPhoneError]=useState("");
      const [motorTypeError, setMotorTypeError]=useState("");
      const [motorError, setMotorError]=useState("");
      const [landAddError, setLandAddError]=useState("");
      const [pwdError, setPasswordError]=useState("");
      const [confirmPwdError, setConfimPwdError]=useState("");
      const [motorHPError, setMotorHPError]=useState("");

    const handleSubmit = e => {

      if(!imeiNumber){setIMEIError("Please Enter IMEI Number")}else{setIMEIError("")}
      if(!firstName){setFirsNameError("Please Enter FirstName")}else{setFirsNameError("")}
      if(!lastName){setLastNameError("Please Enter Lastname")}else{setLastNameError("")}
      if(!userName){setUserNameError("Please Enter Username")}else{setUserNameError("")}
      if(!farmerAddress){setFarmerAddError("Please Enter Farmer Address")}else{setFarmerAddError("")}
      if(!farmerPhoneNumber){setFarmerPhoneError("Please Enter Farmer phone")}else{setFarmerPhoneError("")}
      if(!RMSPhoneNumber){setRMSPhoneError("Please Enter RMS Phone")}else{setRMSPhoneError("")}
      if(!motorType){setMotorTypeError("Please Enter Motor Type")}else{setMotorTypeError("")}
      if(!motor){setMotorError("Please Enter Motor")}else{setMotorError("")}
      if(!landAddress){setLandAddError("Please Enter Landaddress")}else{setLandAddError("")}
      if(!password){setPasswordError("Please Enter Password")}else{setPasswordError("")}
      if(!confirmPassword){setConfimPwdError("Please Cofirm Password")}else{setConfimPwdError("")}
      if(!motorHP){setMotorHPError("Please Motor HP")}else{setMotorHPError("")}

    e.preventDefault();
    if(isRegistration){
      setIsLoading(true);
      registration({
        Imei: imeiNumber,
        FirstName: firstName,
        LastName: lastName,
        UserName: userName,
        FarmerPhNo: farmerPhoneNumber,
        RMSPhNo: RMSPhoneNumber,
        MotorType: motorType,
        MotorHP: motorHP,
        MotorCurrentType: motor,
        Latitude: locationLat,
        Longitude: locationLong,
        LandAddress: landAddress,
        FarmerAddress: farmerAddress,
        Password: password,
        ConfirmPassword: confirmPassword,
   
      }).then(resp => navigateToHome(resp)); 
    }
    else{
      setErrorRegister("Please enter required fields");
    }
            
  };
const navigateToHome = data => {
    setIsLoading(false);
    let result=data?data.payload.status:"";
    console.log("resp register", result);
    if(result=="success"){
        history.push("/Login")
    }
    else{
       setErrorRegister("Error while registration");
    }
  };

const inputStyle = { WebkitBoxShadow: "0 0 0 1000px white inset" }; 

    
    
    const MotorTypes = [ 
        { Name: "Fuji",  Value:"1" }, 
        { Name: "Veichi", Value:"2" }, 
        { Name: "CG",  Value:"3" }, 
             ];
     const Motors = [ 
        { Name: "AC",  Value:"1" }, 
        { Name: "DC", Value:"2" }, 
             ];
  return ( isLoading? <div className={classes.spinnerDiv}><Spinner /></div>:

    <div className={styleFromHomepage?styleFromHomepage:classes.root}> 

     <div><Typography className={classes.loginHeader} ><strong>FARMER DETAILS</strong></Typography></div> 

        <Grid container> 

            <Grid lg={4} sm={4} item /> 
            <Grid item lg={8} sm={8} xs={12}> 
              <Card> 
                <CardContent> 
               <form onSubmit={handleSubmit} >   
      <Grid container >
     <Grid item lg={6} sm={6} xs={12} >
      <Grid item lg={12} sm={12} xs={12} >
            <div className={classes.width98}>
              <div className={classes.lblDiv}><Typography style={{marginBottom:"-10px"}}> First Name  </Typography> </div>
                <div style={{textAlign:"right"}}><Typography className={classes.errorLbl} >{firstNameError?firstNameError:""}</Typography></div>
                <TextField 

                          placeholder="Enter first name" 
                          margin="normal" 
                          variant="outlined"
                          className={classes.txtStyle}
                          value="" 
                          value={firstName}
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleFirstName(e.target.value)}
                /> 
      </div>     
      </Grid>
<Grid item lg={12} sm={12} xs={12} >
            <div className={classes.width98}>
            <div className={classes.lblDiv}><Typography style={{marginBottom:"-10px"}}> Last Name  </Typography> </div>
                <div style={{textAlign:"right"}}><Typography className={classes.errorLbl} >{lastNameError?lastNameError:""}</Typography></div>
                <TextField 

                          placeholder="Enter last name" 
                          margin="normal" 
                          variant="outlined"
                          className={classes.txtStyle}
                          value="" 
                          value={lastName}
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleLastName(e.target.value)}
                /> 
      </div>     
      </Grid>
    <Grid item lg={12} sm={12} xs={12} >
            <div className={classes.width98}> 
            <div className={classes.lblDiv}><Typography style={{marginBottom:"-10px"}}> Farmer Phone Number  </Typography> </div>
                <div style={{textAlign:"right"}}><Typography className={classes.errorLbl} >{farmerPhoneError?farmerPhoneError:""}</Typography></div>
      <TextField 
                          placeholder="Enter farmer phone number" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value={farmerPhoneNumber} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleFarmerPhoneNumber(e.target.value)} 
                        />  
      </div>     
      </Grid>
   <Grid item lg={12} sm={12} xs={12} >
            <div className={classes.width98}> 
            <div className={classes.lblDiv}><Typography style={{marginBottom:"-10px"}}> RMS Phone Number  </Typography> </div>
                <div style={{textAlign:"right"}}><Typography className={classes.errorLbl} >{rmsPhoneError?rmsPhoneError:""}</Typography></div>
                     <TextField 
                          placeholder="Enter RMS  phone number" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={RMSPhoneNumber} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleRMSPhoneNumber(e.target.value)} 
                        /> 
      </div>     
      </Grid>
<Grid item lg={12} sm={12} xs={12} >
            <div className={classes.width98}>
            <div className={classes.lblDiv}><Typography style={{marginBottom:"-10px"}}> IMEI Number  </Typography> </div>
                <div style={{textAlign:"right"}}><Typography className={classes.errorLbl} >{imeiError?imeiError:""}</Typography></div>
                     <TextField 
                          placeholder="Enter IMEI  number" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={imeiNumber} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleIMEI(e.target.value)} 
                        /> 
      </div>     
      </Grid>
 <Grid item lg={12} sm={12} xs={12} >
            <div className={classes.width98}>
            <div className={classes.lblDiv}><Typography style={{marginBottom:"5px"}}> Motor Type  </Typography> </div>
                <div style={{textAlign:"right"}}><Typography className={classes.errorLbl} >{motorTypeError?motorTypeError:""}</Typography></div>
                    <DropDown cssClass={classes.selectDropDown} func={handleMotorType} displayText={MotorTypes} /> 
      </div>     
      </Grid>
  <Grid item lg={12} sm={12} xs={12} >
            <div className={classes.width98}> 
            <div className={classes.lblDiv}><Typography style={{marginBottom:"5px"}}> Motor   </Typography> </div>
                <div style={{textAlign:"right"}}><Typography className={classes.errorLbl} >{motorError?motorError:""}</Typography></div>
                    <DropDown cssClass={classes.selectDropDown} func={handleMotor} displayText={Motors} /> 
      </div>     
      </Grid>

 
      </Grid>
      <Grid item lg={6} sm={6} xs={12} >
      
          
    <Grid item lg={12} sm={12} xs={12} >
            <div className={classes.regRightDiv}>
            <div className={classes.lblDiv}><Typography style={{marginBottom:"-10px"}}> Motor HP   </Typography> </div>
                <div style={{textAlign:"right"}}><Typography className={classes.errorLbl} >{motorHPError?motorHPError:""}</Typography></div>
      <TextField 
                          placeholder="Enter motor HP" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={motorHP} 
                            inputProps={{ style: inputStyle }}
                          onChange={e => handleMotorHP(e.target.value)} 
                        />  
      </div>     
      </Grid>
  
 <Grid item lg={12} sm={12} xs={12} >
            <div className={classes.regRightDiv}>
            <div className={classes.lblDiv}><Typography style={{marginBottom:"-10px"}}> Land Address   </Typography> </div>
                <div style={{textAlign:"right"}}><Typography className={classes.errorLbl} >{landAddError?landAddError:""}</Typography></div>
                     <TextField 
                          placeholder="Enter land address" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={landAddress} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleLandAddress(e.target.value)} 
                        /> 
      </div>     
      </Grid>
 <Grid item lg={12} sm={12} xs={12} >
            <div className={classes.regRightDiv}> 
            <div className={classes.lblDiv}><Typography style={{marginBottom:"-10px"}}> Farmer Address   </Typography> </div>
                <div style={{textAlign:"right"}}><Typography className={classes.errorLbl} >{farmerAddError?farmerAddError:""}</Typography></div>
                     <TextField 
                          placeholder="Enter address" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={farmerAddress} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleFarmerAddress(e.target.value)} 
                        /> 
      </div>     
      </Grid>

 <Grid item lg={12} sm={12} xs={12} >
            <div className={classes.regRightDiv}> 
            <div className={classes.lblDiv}><Typography style={{marginBottom:"-10px"}}> User Name   </Typography> </div>
                <div  style={{textAlign:"right"}}><Typography className={classes.errorLbl} >{userNameError?userNameError:""}</Typography></div>
                     <TextField 
                          placeholder="Enter username" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={userName} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleUsername(e.target.value)} 
                        /> 
      </div>     
      </Grid> <Grid item lg={12} sm={12} xs={12} >
            <div className={classes.regRightDiv}>
            <div className={classes.lblDiv}><Typography style={{marginBottom:"-10px"}}> Password   </Typography> </div>
                <div style={{textAlign:"right"}}><Typography className={classes.errorLbl} >{pwdError?pwdError:""}</Typography></div>
                     <TextField 
                          placeholder="Enter password" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={password} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handlePassword(e.target.value)} 
                        /> 
      </div>     
      </Grid> 
<Grid item lg={12} sm={12} xs={12} >
            <div className={classes.regRightDiv}> 
            <div className={classes.lblDiv}><Typography style={{marginBottom:"-10px"}}> Confirm Password   </Typography> </div>
                <div style={{textAlign:"right"}}><Typography className={classes.errorLbl} >{confirmPwdError?confirmPwdError:""}</Typography></div>
                     <TextField 
                          placeholder="Re-Enter password" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={confirmPassword} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleConfirmPassword(e.target.value)} 
                        /> 
      </div>     
      </Grid>
<Grid item lg={12} sm={12} xs={12} >
    
  
            <div className={classes.regRightDiv}> 
            <div className={classes.lblDiv}><Typography style={{marginBottom:"-10px"}}> Location   </Typography> </div>
                <div ><Typography className={classes.errorLbl} ></Typography></div>
        <Grid container>
<Grid item lg={6} sm={6} xs={6} style={{paddingRight:"5px"}} >
    <TextField 
                          placeholder="Enter lat" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={locationLat} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleLocationLat(e.target.value)} 
                        /> 
    
    </Grid>
    <Grid item lg={6} sm={6} xs={6} style={{paddingLeft:"5px"}} >
        
        
        <TextField 
                          placeholder="Enter long" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={locationLong} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleLocationLongt(e.target.value)} 
                        /> 
        </Grid>    
        </Grid>
                     
      </div>     
      </Grid>
      </Grid>
      </Grid>
                 <Grid container>
                     
 <Grid item lg={12} sm={12} xs={12} >
            <div><Button className={classes.btnStyle} type="submit"> REGISTER</Button>
      </div>     
      </Grid>
      <Grid item lg={12} sm={10} xs={12} style={{marginBottom:"20px"}} >
            <div className={classes.errorClass} >{errorRegister?errorRegister:""}</div>
         
      </Grid>
      </Grid>


                  


                   

                  

                  
                    

                  </form> 

                </CardContent> 

              </Card> 

            </Grid> 

          </Grid> 

    </div> 

  ); 

} 

 

RegisterComponent.propTypes = { 

  classes: PropTypes.object.isRequired 

}; 

 

export default withStyles(styles)(withRouter(RegisterComponent)); 
