import React from "react"; 
//import DashBoardComponent from "../../components/DashBoard";DashBoardNew
import { connect } from "react-redux"; 
 import {setMoterData} from "../../actions/DataTable"; 
import {GetPumpReading} from "../../actions/Dashboard";

import DashBoardComponentNew from "../../components/DashBoard/DashBoardNew";

const Dashboard = ({setMoterData,motorReaddingData,GetPumpReading}) => { 
  return ( 
    <div>
     <DashBoardComponentNew motorReaddingData={motorReaddingData} setMoterData={setMoterData} GetPumpReading={GetPumpReading}/> 
    </div> 
  ); 
}; 

export default connect(({ dashboard }) => ({motorReaddingData:dashboard.motorReaddingData}),{setMoterData,GetPumpReading })(Dashboard); 

